* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

.App {
  background: url(https://images.pexels.com/photos/942802/pexels-photo-942802.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940)
    no-repeat center center/cover;
  transform: rotate(180deg);
  position: absolute;
  top: 50px;
  left: 0;
  width: 100%;
  height: 94%;
}

.nav {
  background-color: rgb(249, 249, 252);
  color: red;
  opacity: 19%;
  list-style: none;
  text-align: right;
  padding: 20px 10px 20px 10px;
}

.nav > li {
  display: inline-block;
  padding: 0 15px 0 15px;
  font-size: 12px;
}

.nav > li > a {
  text-decoration: none;
  color: black;
  font-size: 18px;
}

.nav > li > a:hover {
  color: darkgrey;
  font-size: 17px;
  transition-delay: 0.2s;
}

.logo {
  color: white;
  float: left;
  padding-left: 25px;
  font-size: 16px;
  font-weight: bold;
}

.logo > a {
  text-decoration: none;
  color: black;
}
